
<template>
<div>
    <h1>Editando la opini贸n del juego {{opinion.nameGame}}</h1>
    <div>
        <form action="">
            <div class="title-nombre">
                <label for="">Nombre</label>
            </div>
            <input type="text" v-model="newName">    
            <div class="title-opinion">
                <label for="">Opini贸n</label>
            </div>
            <textarea name="" id="" cols="30" rows="10" v-model="newOpinion"></textarea>
        </form>
    </div>
    <div>
        <button class="btn btn-secondary btn-md" @click="ReturnOpinion">Regresar</button>
        <button class="btn btn-info btn-md" @click="SaveBack">Guardar</button>
    </div>
</div>
    
</template>

<script>
import {mapActions, mapState} from 'vuex'
export default {
    name: 'opinion-edit',
    props: {
        index: {
            type: Number
        }
    },
    data: function(){
        return {
            newOpinion: "",
            newGame:"",
            newName:""
        }
    },
    computed: {
        ...mapState(['opiniones']),
        opinion(){
            return this.opiniones[this.index]
        }
    },
    methods: {
        ...mapActions(['saveOpinion']),
        ReturnOpinion(){
            this.$router.push('/admin-view')
        },
        SaveBack(){
            let opinion ={
                opinion: this.newOpinion,
                nameGame: this.newGame,
                name: this.newName
            }
            this.saveOpinion({index:this.index, opinion:opinion})
            this.ReturnOpinion()
        }
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
        created(){
            console.log(this.opiniones[this.index])
            this.newOpinion = this.opiniones[this.index].opinion
            this.newGame = this.opiniones[this.index].nameGame
            this.newName = this.opiniones[this.index].name
            }
    // -- End Lifecycle Methods
}
</script>

<style scoped>
    button{
        margin: 30px 15px;
        margin-bottom: 150px;
    }
    h1{
        margin: 50px auto;
    }
    .title-nombre,  .title-opinion{
        margin: 20px auto;
    }

</style>